import "./styles.scss";

/** */
export interface AppProps {
  /** App logo to display */
  app: "blend" | "phoenix" | "soroswap" | "stellar" | "stellarx";
  /** Size of the App */
  size: "sm" | "md" | "lg" | "xl";
}

/**
 * `App` shows an apps logo.
 */
export const App: React.FC<AppProps> = ({ app, size }) => {
  const appData = APP_DATA[app];

  if (!appData) {
    return null;
  }

  return (
    <div className={`App App--${size}`}>
      <img src={appData.src} alt={`${appData.alt} logo`} />
    </div>
  );
};

App.displayName = "App";

const APP_DATA = {
  blend: {
    alt: "Blend",
    src: "data:image/png;base64,",
  },
  phoenix: {
    alt: "Phoenix",
    src: "data:image/png;base64,",
  },
  soroswap: {
    alt: "Soroswap",
    src: "data:image/png;base64,",
  },
  stellar: {
    alt: "Stellar",
    src: "data:image/png;base64,",
  },
  stellarx: {
    alt: "StellarX",
    src: "data:image/png;base64,",
  },
};
